import type {
  MoSerializationPlugin, MoServerSerializationPlugin,
} from '@ali/4ever-cangjie';
import jsonMLToValue from './jsonMLToValue';
import valueToJsonML from './valueToJsonML';
import htmlToJsonML from './htmlToJsonML';
import jsonMLToHTML from './jsonMLToHTML';
import valueToText from './valueToText';
import markdownToHTML from './markdownToHTML';
import htmlToMarkdown from './htmlToMarkdown';
import * as constants from './constants';

export default function createSerializer(): MoSerializationPlugin {
  return {
    jsonMLToValue,
    valueToJsonML,
    htmlToJsonML,
    jsonMLToHTML,
    valueToText,
    markdownToHTML,
    htmlToMarkdown,
  };
}

// 兼容原 mo 里的 table......
createSerializer.constants = constants;

export function createServerSerializationPlugin(): MoServerSerializationPlugin {
  return {
    valueToJsonML,
    jsonMLToValue,
  };
}
